import {checkMobile, returnLimitRange} from "./utils";

const useControls = (camera) => {
	camera.rotation.order = "YXZ";
	let is_mouse_down = false;
	let previous_move_x = undefined;
	let previous_move_y = undefined;
	let is_mobile = checkMobile();

	// 适配移动端事件
	let down_event_name, move_event_name, up_event_name;
	if (is_mobile) {
		down_event_name = "touchstart";
		move_event_name = "touchmove";
		up_event_name = "touchend";
	} else {
		down_event_name = "mousedown";
		move_event_name = "mousemove";
		up_event_name = "mouseup";
	}

	// 监听鼠标按下事件
	window.addEventListener(
		down_event_name,
		(e) => {
			previous_move_x = is_mobile ? e.touches[0].screenX : e.screenX
			previous_move_y = is_mobile ? e.touches[0].screenY : e.screenY
			is_mouse_down = true;
		},
		false
	);

	// 监听鼠标抬起事件
	window.addEventListener(
		up_event_name,
		() => {
			is_mouse_down = false;
		},
		false
	);

	// 监听鼠标移动事件
	window.addEventListener(
		move_event_name,
		(e) => {
			if (is_mouse_down) {
				const cur_move_x = is_mobile ? e.touches[0].screenX : e.screenX;
				const movement_x = is_mobile ? ((cur_move_x - previous_move_x) * 0.5) : e.movementX * 0.8;
				previous_move_x = cur_move_x
				const cur_move_y = is_mobile ? e.touches[0].screenY : e.screenY;
				const movement_y = is_mobile ? ((cur_move_y - previous_move_y) * 0.5) : e.movementY * 0.8;
				previous_move_y = cur_move_y

				camera.rotation.y = camera.rotation.y + (movement_x / window.innerWidth) * Math.PI;
				camera.rotation.x = returnLimitRange(-Math.PI / 2, Math.PI / 2, camera.rotation.x + (movement_y / window.innerHeight) * Math.PI);
			}
		},
		false
	);
}

export default useControls;
