import {Raycaster, Vector2, Vector3} from "three";
import gsap from "gsap";

// 相机所处房间位置
const RoomPosition = {
	"阳台": new Vector3(0, 0, 15),
	"客厅": new Vector3(0, 0, 0),
	"厨房": new Vector3(-5, 0, -10),
	"走廊": new Vector3(-15, 0, 0),
	"主卧": new Vector3(-25, 0, 2),
}

const raycastSprite = (camera, objects) =>{
	const raycaster = new Raycaster();
	const mouse = new Vector2();

	// 事件的监听
	window.addEventListener("click", (event) => {
		mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
		mouse.y = -((event.clientY / window.innerHeight) * 2 - 1);

		raycaster.setFromCamera(mouse, camera);

		const intersects = raycaster.intersectObjects(objects);

		if (intersects.length > 0 && intersects[0].object.isSprite) {
			const sprite = intersects[0].object;
			gsap.to(camera.position, {
				x: RoomPosition[sprite.name].x,
				y: RoomPosition[sprite.name].y,
				z: RoomPosition[sprite.name].z,
				duration: 1,
			});
		}
	});
}

export default raycastSprite;
