import {DefaultLoadingManager} from "three";
import gsap from "gsap";

const useLoader = () => {
	let loading_html = document.createElement("div");
	loading_html.id = "loading";
	loading_html.innerHTML = `
		<div class="progress-bar">
			<div class="dot"></div>
		</div>
		<span class="percentage">0%</span>
	`;
	document.body.appendChild(loading_html);

	let progress_dom = loading_html.children[0].children[0];

	let progress_text_dom = loading_html.children[1];

	DefaultLoadingManager.onProgress = function (item, loaded, total) {
		const percentage = (loaded / total * 100).toFixed(2);
		progress_dom.style.width = `${percentage}%`;
		progress_text_dom.innerText = `${percentage}%`;

		if (loaded === total) {
			gsap.to(loading_html, {
				opacity: 0,
				delay: 1,
				duration: 0.8,
				onComplete: () => {
					document.body.removeChild(loading_html)
					loading_html = null;
					progress_dom = null;
					progress_text_dom = null;
				},
			});
		}
	};
};

export default useLoader;
