import {Mesh, BoxGeometry, MeshBasicMaterial, TextureLoader, Vector2} from "three";

const texture_loader = new TextureLoader();

const texture_arr = ["l", "r", "u", "d", "b", "f"];

const createRoom = ({
	name,
	texture_url,
	position,
	euler
}) => {
	const box_materials = [];

	texture_arr.forEach(item => {
		const texture = texture_loader.load(`${texture_url}/${item}.jpg`);
		if (item === 'd' || item === 'u') {
			texture.rotation = Math.PI;
			texture.center = new Vector2(0.5, 0.5);
		}
		box_materials.push(new MeshBasicMaterial({map: texture}));
	});

	const cube = new Mesh(
		new BoxGeometry(10, 10, 10),
		box_materials
	);
	cube.name = name;
	cube.geometry.scale(1, 1, -1);
	cube.position.copy(position);
	cube.rotation.copy(euler);

	return cube;
}

export default createRoom;
